import React, { useEffect, useState } from "react";
import YouTube from "react-youtube";
import styles from "./Main.module.css";
import { ReactComponent as Logo } from "../assets/logo.svg";
import Footer from "../components/Footer";
import { useNavigate } from "react-router-dom";
import iconUserProfile from "../assets/icon_userprofile.png";
import { Link } from "react-router-dom";

function Main() {
  const navigate = useNavigate();
  const [userInfo, setUserInfo] = useState({
    nickname: "", //  
    profileImage: iconUserProfile, //  
  });

  // localStorage   .
  useEffect(() => {
    const storedUserInfo = localStorage.getItem("userInfo");
    if (storedUserInfo) {
      const parsedUserInfo = JSON.parse(storedUserInfo);
      setUserInfo(parsedUserInfo);
    }
  }, []);

  const handleProfileClick = () => {
    if (!userInfo.nickname) {
      navigate("/kakaoLogin"); //     
    } else {
      navigate("/mypage"); //    
    }
  };

  const option = {
    height: "270", // 
    width: "480", // 
    playerVars: {
      autoplay: 0,
      mute: 1,
    },
  };

  return (
    <div className={styles.screen}>
      <div className={styles.Header}>
        <div className={styles.logoContainer}>
          <Logo className={styles.logo} />
          <h2 className={styles.logotitle}>, </h2>
        </div>
        <div
          className={styles.profileContainer}
          onClick={handleProfileClick} //    
        >
          <img
            src={userInfo.profileImage || iconUserProfile}
            alt="User Profile"
            className={styles.profileImage}
          />
          <span className={styles.profileName}>
            {userInfo.nickname || " "}
          </span>
        </div>
      </div>

      <div className={styles.mainButtons}>
        <Link to="/new" className={styles.button}>
            
        </Link>
        <Link to="/mypage" className={styles.button}>
          
        </Link>
        <Link to="/kakaoLogin" className={styles.button}>
          
        </Link>
      </div>

      {/* YouTube  */}
      <div className={styles.youtubeContainer}>
        <YouTube
          videoId="ZVmmP0CgpUg" // YouTube  ID
          opts={option}
          className={styles.youtube}
        />
        <YouTube
          videoId="S39KiBTpIaM" // YouTube  ID
          opts={option}
          className={styles.youtube}
        />
        <YouTube
          videoId="M87B-PM2JNs" // YouTube  ID
          opts={option}
          className={styles.youtube}
        />
      </div>

      <Footer />
    </div>
  );
}

export default Main;
