import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Sidebar from "../components/Sidebar";
import SidebarTabs from "../components/SidebarTabs";
import TravelSummary from "../components/TravelSummary";
import DetailedSchedule from "../components/DetailedSchedule";
import Route from "../components/Route";
import Checklist from "../components/Checklist";
import Modal from "../components/Modal";
import styles from "./DetailedItineraryPage.module.css";
import KakaoMap from "../components/KakaoMap";

function DetailedItineraryPage() {
  const [activeTab, setActiveTab] = useState(" ");
  const [isModalOpen, setIsModalOpen] = useState(false);
  const navigate = useNavigate();

  const tabs = [
    { label: " ", content: <TravelSummary /> },
    { label: " ", content: <DetailedSchedule /> },
    { label: "", content: <Route /> },
    { label: "", content: <Checklist /> },
  ];

  //  
  const handleConfirmClick = () => {
    setIsModalOpen(true);
  };

  //   (X )
  const handleModalClose = () => {
    setIsModalOpen(false);
  };

  //   ( )
  const handleConfirm = () => {
    setIsModalOpen(false); //  
    navigate("/mypage"); //  
  };

  return (
    <div className={styles.detailedItineraryPage}>
      {/*    */}
      <div className={styles.sidebarContainer}>
        <Sidebar>
          <SidebarTabs
            tabs={tabs}
            activeTab={activeTab}
            onTabClick={setActiveTab}
          />
        </Sidebar>
      </div>

      {/*    */}
      <div className={styles.mapContainer}>
        <KakaoMap />
        <button className={styles.confirmButton} onClick={handleConfirmClick}>
           
        </button>
      </div>

      {/*   */}
      {isModalOpen && (
        <Modal
          title=" "
          message="   .  ?"
          onClose={handleModalClose}
          onConfirm={handleConfirm}
        />
      )}
    </div>
  );
}

export default DetailedItineraryPage;
